.professors {
  &__container {
    position: relative;
    padding: 13rem 0;
    @include respond(phone) {
      padding: 8rem 0;
    }
  }
  &__text-container {
    max-width: 65rem;
    color: $color-white;
    padding: 0 3rem;
    @include respond(tab-port) {
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    @include respond(phone) {

    }
  }

  &__text {
    margin-bottom: 5rem;
    position: relative;
    z-index: 10;
  }

  &__image {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 33.5rem;
    height: 48rem;

    @include respond(tab-port) {
      display: none;
    }
  }
}
