@charset "UTF-8";
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 60%;
  }
}
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 58%;
  }
}
a,
a:hover,
a:active,
a:focus {
  text-decoration: none;
  outline: none;
}

ul {
  list-style: none;
}

body {
  box-sizing: border-box;
  position: relative;
}

.wrapper {
  max-width: 100%;
  overflow-x: hidden;
  position: relative;
  z-index: 1;
}

section {
  background-color: #fafafa;
  position: relative;
}

body {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  line-height: 1.7;
  color: #333;
  font-size: 1.6rem;
  background-color: #fafafa;
}

.heading-primary {
  font-size: 4.2rem;
}
@media only screen and (max-width: 75em) {
  .heading-primary {
    font-size: 3.2rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .heading-primary {
    font-size: 2.8rem;
  }
}

.heading-secondary,
.heading-secondary-white {
  font-size: 3.6rem;
  position: relative;
  text-align: center;
  color: #000;
  margin-bottom: 5rem;
}
@media only screen and (max-width: 75em) {
  .heading-secondary,
.heading-secondary-white {
    font-size: 3rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .heading-secondary,
.heading-secondary-white {
    font-size: 2.4rem;
  }
}
.heading-secondary::after,
.heading-secondary-white::after {
  content: "";
  position: absolute;
  bottom: -3rem;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 2px;
  background-color: #0068b7;
}
.heading-secondary-white,
.heading-secondary-white-white {
  color: #fff;
}
.heading-secondary-white::after,
.heading-secondary-white-white::after {
  background-color: #fff;
}
.heading-tertiary {
  font-size: 3rem;
  margin-bottom: 6rem;
  position: relative;
}
@media only screen and (max-width: 75em) {
  .heading-tertiary {
    font-size: 2.6rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .heading-tertiary {
    text-align: center;
  }
}
@media only screen and (max-width: 37.5em) {
  .heading-tertiary {
    font-size: 2rem;
  }
}
.heading-tertiary::after {
  content: "";
  position: absolute;
  bottom: -3rem;
  left: 0;
  width: 100px;
  height: 2px;
  background-color: #fff;
}
@media only screen and (max-width: 56.25em) {
  .heading-tertiary::after {
    left: 50%;
    transform: translateX(-50%);
  }
}

.u-center-text {
