}

.button {
  width: 22rem;
  height: 5rem;
  border: none;
  border-radius: 2.5rem;
  color: #fff;
  transition: all 0.3s;
  cursor: pointer;
  position: relative;
  z-index: 10;
}
.button__primary {
  color: #0068b7;
  border: 1px solid #0068b7;
  background: linear-gradient(130deg, transparent 0%, transparent 50%, #0068b7 50%, #0068b7 50%);
  background-size: 210%;
  background-position: 0;
}
.button__primary:hover {
  color: #fff;
  background-position: 100%;
  border: none;
  transform: translateY(-2px);
}
.button__primary-bg {
  background: linear-gradient(130deg, #0068b7 0%, #0068b7 50%, #fff 50%, #fff 50%);
  background-size: 210%;
  background-position: 0;
}
.button__primary-bg:hover {
  background-position: 100%;
  color: #0068b7;
  border: 1px solid #0068b7;
}
.button__secondary-bg {
  background: linear-gradient(130deg, #fdd000 0%, #fdd000 50%, #fff 50%, #fff 50%);
  background-size: 210%;
  background-position: 0;
}
.button__secondary-bg:hover {
  background-position: 100%;
  color: #fdd000;
  border: 1px solid #fdd000;
}
.button__white {
  border: 1px solid #fff;
  background-color: transparent;
}
.button__white:hover {
  color: #0068b7;
  background-color: #fff;
  border: 1px solid #0068b7;
  transform: translateY(-2px);
}

.input {
  width: 35rem;
  height: 4.4rem;
  padding: 2rem 3rem;
  border-radius: 2.5rem;
  border: none;
  margin-right: 1rem;
  background-color: #fff;
  color: #888;
  position: relative;
  z-index: 10;
}

.select {
  width: 35rem;
  height: 4.4rem;
  border-radius: 2.5rem;
  padding: 0 3rem;
  margin-bottom: 2rem;
  border: 1px solid #555;
  position: relative;
  z-index: 10;
}

.textarea {
  background-color: #fff;
  color: #888;
  width: 44.5rem;
  height: 16.5rem;
  padding: 2rem 3rem;
  display: block;
  position: relative;
  z-index: 10;
}

@media only screen and (max-width: 75em) {
  .input,
.select,
