}

.swiper-button-prev, .swiper-button-next {
  color: #fff;
}
.swiper-button-prev {
  left: 2rem;
}
.swiper-button-next {
  right: 2rem;
}

.swiper-button-container {
  overflow: visible;
  margin: 0 auto;
  max-width: 90vw;
  height: 100%;
  position: relative;
}
.swiper-button-container .swiper-button-prev {
  left: -1rem;
}
.swiper-button-container .swiper-button-next {
