}
.swiper-container-hero .swiper-slide {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  background-size: cover;
}
.swiper-container-hero .swiper-slide:nth-of-type(1) {
  background: linear-gradient(rgba(0, 0, 0, 0.95) 0%, rgba(0, 0, 0, 0.8) 12%, rgba(0, 0, 0, 0.5) 100%), url("../img/hero-bg3.jpg");
}
.swiper-container-hero .swiper-slide:nth-of-type(2) {
  background: linear-gradient(rgba(0, 0, 0, 0.95) 0%, rgba(0, 0, 0, 0.8) 12%, rgba(0, 0, 0, 0.5) 100%), url("../img/hero-bg1.jpg");
}
.swiper-container-hero .swiper-slide:nth-of-type(3) {
  background: linear-gradient(rgba(0, 0, 0, 0.95) 0%, rgba(0, 0, 0, 0.8) 12%, rgba(0, 0, 0, 0.5) 100%), url("../img/hero-bg2.jpg");
}
.swiper-container-hero .swiper-slide:nth-of-type(4) {
  background: linear-gradient(rgba(0, 0, 0, 0.95) 0%, rgba(0, 0, 0, 0.8) 12%, rgba(0, 0, 0, 0.5) 100%), url("../img/hero-bg3.jpg");
}
.swiper-container-hero .swiper-slide:nth-of-type(5) {
  background: linear-gradient(rgba(0, 0, 0, 0.95) 0%, rgba(0, 0, 0, 0.8) 12%, rgba(0, 0, 0, 0.5) 100%), url("../img/hero-bg1.jpg");
}

.swiper-container-awards {
  height: 100%;
  margin: 0 auto;
  overflow: hidden;
}
.swiper-container-awards .swiper-slide {
  display: flex;
  justify-content: space-evenly;
  align-content: center;
  margin-bottom: 3rem;
}

