.container {
  width: 90%;
  margin: 0 auto;
}

.row {
  max-width: 114rem;
  margin: 0 auto;
}
.row::after {
  content: "";
  display: table;
  clear: both;
}
.row [class^=col-] {
  float: left;
}
.row [class^=col-]:not(:last-child) {
  margin-right: 6rem;
}
@media only screen and (max-width: 56.25em) {
  .row [class^=col-]:not(:last-child) {
    margin-right: 0;
    margin-bottom: 6rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .row [class^=col-] {
    width: 100% !important;
  }
}
.row .col-1-of-2 {
  width: calc((100% - 6rem) / 2);
}
.row .col-1-of-3 {
  width: calc((100% - 2 * 6rem) / 3);
}
.row .col-2-of-3 {
  width: calc( 2 * ((100% - 2 * 6rem) / 3) + 6rem );
}
.row .col-1-of-4 {
  width: calc((100% - 3 * 6rem) / 4);
}
.row .col-2-of-4 {
  width: calc( 2 * ((100% - 3 * 6rem) / 4) + 6rem );
}
.row .col-3-of-4 {
  width: calc( 3 * ((100% - 3 * 6rem) / 4) + 2 * 6rem );
}

.row-wide {
  max-width: 133rem;
  height: 100%;
  margin: 0 auto;
  overflow: visible;
}
.row-wide:not(:last-child) {
  margin-bottom: 8rem;
}
@media only screen and (max-width: 56.25em) {
  .row-wide:not(:last-child) {
    margin-bottom: 6rem;
  }
}
.row-wide::after {
  content: "";
  display: table;
  clear: both;
}
.row-wide [class^=col-] {
  float: left;
}
.row-wide [class^=col-]:not(:last-child) {
  margin-right: 6rem;
}
@media only screen and (max-width: 56.25em) {
  .row-wide [class^=col-]:not(:last-child) {
    margin-right: 0;
    margin-bottom: 6rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .row-wide [class^=col-] {
    width: 100% !important;
  }
}
.row-wide .col-1-of-2 {
  width: calc((100% - 6rem) / 2);
}
.row-wide .col-1-of-3 {
  width: calc((100% - 2 * 6rem) / 3);
}
.row-wide .col-2-of-3 {
  width: calc( 2 * ((100% - 2 * 6rem) / 3) + 6rem );
}
.row-wide .col-1-of-4 {
  width: calc((100% - 3 * 6rem) / 4);
}
.row-wide .col-2-of-4 {
  width: calc( 2 * ((100% - 3 * 6rem) / 4) + 6rem );
}
.row-wide .col-3-of-4 {
  width: calc( 3 * ((100% - 3 * 6rem) / 4) + 2 * 6rem );
}

.header {
  height: 10rem;
  line-height: 10rem;
  background-color: rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10000;
}
@media only screen and (max-width: 75em) {
  .header {
    height: 6rem;
    line-height: 6rem;
  }
}
.header-bg {
  background-color: rgba(0, 0, 0, 0.7);
  padding: 0 2rem;
  height: 6rem;
  line-height: 6rem;
}
.header-bg .header__logo {
  width: 10rem;
  height: 2.8rem;
  margin-left: 2rem;
}
.header-bg .header__nav {
  margin-right: 0;
}
.header-bg .header__nav-item {
  height: 6rem;
  line-height: 6rem;
}
.header__logo {
  width: 14.7rem;
  height: 3.3rem;
}
.header__logo-link {
  margin: auto 4rem;
  line-height: 0;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  margin-right: 3rem;
}
@media only screen and (max-width: 75em) {
  .header__nav {
    height: 6rem;
    line-height: 6rem;
  }
}
.header__nav-container {
  display: flex;
}
@media only screen and (max-width: 75em) {
  .header__nav-container {
    opacity: 0;
    visibility: hidden;
  }
}
.header__nav-list {
  display: flex;
}
@media only screen and (max-width: 75em) {
  .header__nav-list {
    flex-direction: column;
    max-height: 100%;
  }
}
.header__nav-item {
  padding: 0 1.5rem;
  display: block;
  height: 10rem;
  line-height: 10rem;
}
.header__nav-item:last-child {
  padding-right: 0;
  position: relative;
}
.header__nav-item:last-child:after {
  content: "|";
  color: #fff;
  margin-left: 3rem;
}
@media only screen and (max-width: 75em) {
  .header__nav-item:last-child:after {
    content: "—";
    position: absolute;
    bottom: 0;
    right: 50%;
    transform: translate(50%, 50%);
  }
}
@media only screen and (max-width: 75em) {
  .header__nav-item {
    height: 6rem;
    line-height: 6rem;
    text-align: center;
    width: 25rem;
    margin: 0 auto;
    transform: translateX(-100rem);
    transition: transform 0.5s cubic-bezier(0.59, 0.59, 0.63, 1.46);
  }
  .header__nav-item:nth-of-type(1) {
    transition-delay: 70ms;
  }
  .header__nav-item:nth-of-type(2) {
    transition-delay: 140ms;
  }
  .header__nav-item:nth-of-type(3) {
    transition-delay: 210ms;
  }
  .header__nav-item:nth-of-type(4) {
    transition-delay: 280ms;
  }
  .header__nav-item:nth-of-type(5) {
    transition-delay: 350ms;
  }
  .header__nav-item:nth-of-type(6) {
    transition-delay: 420ms;
  }
  .header__nav-item:nth-of-type(7) {
    transition-delay: 490ms;
  }
}
.header__nav-link {
  text-align: center;
  color: #fff;
  transition: all 0.3s;
}
@media only screen and (max-width: 75em) {
  .header__nav-link {
    display: block;
