.header__line {
  display: inline-block;
  width: 2.5rem;
  height: 2px;
  vertical-align: middle;
  background-color: #fff;
  transition: all 0.3s;
}
.header__line::before, .header__line::after {
  content: "";
  display: inline-block;
  position: absolute;
  width: 2.5rem;
  height: 2px;
  background-color: #fff;
  transform: translateX(-50%);
  transition: all 0.3s;
}
.header__line::before {
  top: 1rem;
  left: 50%;
}
.header__line::after {
  top: 3rem;
  left: 50%;
}
@media only screen and (max-width: 75em) {
  .header.active {
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.8);
    font-size: 2.4rem;
