    left: 50%;
    transform: translate(-50%, -50%);
    flex-direction: column;
    align-items: center;
  }
  .header.active .header__nav-container > .sns {
    margin-top: 2rem;
  }
}
.header.active .header__nav-list {
  width: inherit;
}
.header.active .header__nav-item {
  transform: translateX(0);
  width: inherit;
}
.header.active .header__line {
  width: 0px;
}
.header.active .header__line::before {
  transform: translate(-1.25rem, 1rem) rotate(45deg);
}
.header.active .header__line::after {
  transform: translate(-1.25rem, -1rem) rotate(135deg);
}

