// 共通
// buttons
.swiper-button {
  &-prev,
  &-next {
    color: $color-white;
  }
  &-prev {
    left: 2rem;
  }
  &-next {
    right: 2rem;
  }
}

// buttonsを外に出す場合
.swiper-button-container {
  overflow: visible;
  margin: 0 auto;
  // max-width: $grid-width-wide;
  max-width: 90vw;
  height: 100%;
  position: relative; //こっちにrelative

  & .swiper-button {
    &-prev {
      left: -1rem;
      // @media only screen and (max-width: 87em) {
      //   left: 0;
      //   }
    }
    &-next {
      right: -1rem;
      // @media only screen and (max-width: 87em) {
      //   right: 0;
      //   }
    }
  }
}
// pagination bullets
.swiper-pagination-bullet {
  background: $color-grey-light-2;

  &-active {
    background-color: $color-primary;
  }
}

.swiper-container-hero {
  width: 100%;
  height: 100vh;
  & .swiper {
    &-slide {
      position: relative;
      width: 100vw;
      height: 100vh;

      display: flex;
      flex-direction: column;
      justify-content: center;
      align-content: center;

      background-size: cover;
      // prevを押したときの3番目の背景
      &:nth-of-type(1) {
        background: linear-gradient(
            rgba(0, 0, 0, 0.95) 0%,
            rgba(0, 0, 0, 0.8) 12%,
            rgba(0, 0, 0, 0.5) 100%
          ),
          url('../img/hero-bg3.jpg');
      }
      @for $i from 1 through 3 {
        &:nth-of-type(#{$i + 1}) {
          background: linear-gradient(
              rgba(0, 0, 0, 0.95) 0%,
              rgba(0, 0, 0, 0.8) 12%,
              rgba(0, 0, 0, 0.5) 100%
            ),
            url('../img/hero-bg' + $i + '.jpg');
        }
      }

      // next 二周目以降の1番目の背景
      &:nth-of-type(5) {
        background: linear-gradient(
            rgba(0, 0, 0, 0.95) 0%,
            rgba(0, 0, 0, 0.8) 12%,
            rgba(0, 0, 0, 0.5) 100%
          ),
          url('../img/hero-bg1.jpg');
      }
    }
  }
}

.swiper-container-awards {
  height: 100%;
  margin: 0 auto;
  overflow: hidden;

  & .swiper {
    &-slide {
      display: flex;
      justify-content: space-evenly;
      align-content: center;
      margin-bottom: 3rem;
    }
  }
}
