.subscribe {
  &__container {
    position: relative;
    padding: 13rem 0;

    @include respond(phone) {
      padding: 5rem 0;
    }
  }

  &__text-container {
    max-width: 70%;
    padding: 0 3rem;
    @include respond(phone) {
      max-width: 100%;
    }
  }

  &__text {
    margin-bottom: 5rem;
  }

  &__image {
    max-width: 35.2rem;
    height: 47.9rem;
    position: absolute;
    bottom: 0;
    right: 0;
    @include respond(tab-port) {
      max-width: 25rem;
      object-fit: contain;
      margin-bottom: -7rem;
      z-index: 1;
    }
    @include respond(phone) {
      max-width: 16.2rem;
      margin-bottom: -13rem;
    }
  }
}
