.button {
  width: 22rem;
  height: 5rem;
  border: none;
  border-radius: 2.5rem;
  color: $color-white;
  transition: all 0.3s;
  cursor: pointer;
  position: relative;
  z-index: 10;

  &__primary {
    color: $color-primary;
    border: 1px solid $color-primary;
    background: linear-gradient(
      130deg,
      transparent 0%,
      transparent 50%,
      $color-primary 50%,
      $color-primary 50%
    );
    background-size: 210%;
    background-position: 0;
    &:hover {
      color: $color-white;
      background-position: 100%;
      border: none;
      transform: translateY(-2px);
    }
  }

  &__primary-bg {
    background: linear-gradient(
      130deg,
      $color-primary 0%,
      $color-primary 50%,
      $color-white 50%,
      $color-white 50%
    );
    background-size: 210%;
    background-position: 0;
    &:hover {
      background-position: 100%;
      color: $color-primary;
      border: 1px solid $color-primary;
    }
  }

  &__secondary-bg {
    background: linear-gradient(
      130deg,
      $color-secondary 0%,
      $color-secondary 50%,
      $color-white 50%,
      $color-white 50%
    );
    background-size: 210%;
    background-position: 0;
    &:hover {
      background-position: 100%;
      color: $color-secondary;
      border: 1px solid $color-secondary;
    }
  }

  &__white {
    border: 1px solid $color-white;
    background-color: transparent;
    &:hover {
      color: $color-primary;
      background-color: $color-white;
      border: 1px solid $color-primary;
      transform: translateY(-2px);
    }
  }
}
