.input {
  width: 35rem;
  height: 4.4rem;
  padding: 2rem 3rem;
  border-radius: 2.5rem;
  border: none;
  margin-right: 1rem;
  background-color: $color-white;
  color: $color-grey-dark-3;
  position: relative;
  z-index: 10;
}

.select {
  width: 35rem;
  height: 4.4rem;
  border-radius: 2.5rem;
  padding: 0 3rem;
  margin-bottom: 2rem;
  border: 1px solid $color-grey-dark-2;
  position: relative;
  z-index: 10;
}

.textarea {
  background-color: $color-white;
  color: $color-grey-dark-3;
  width: 44.5rem;
  height: 16.5rem;
  padding: 2rem 3rem;
  display: block;
  position: relative;
  z-index: 10;
}

@include respond(tab-land) {
  .input,
  .select,
  .textarea {
    margin-bottom: 2rem;
  }
}
