.footer {
  width: 100vw;
  font-size: 1.2rem;
  padding: 0 3rem;
  position: relative;
  z-index: -1;
  height: 65rem;
  overflow: hidden;
  @include respond('tab-land') {
    height: 60rem;
  }
  @include respond(phone) {
    height: 65rem;
  }
  &__top {
    max-width: $grid-width;
    width: 100%;
    padding: 13rem 0;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    @include respond('tab-land') {
      padding: 8rem 2rem;
    }
    @include respond('tab-port') {
      height: 55rem;
    }
    @include respond(phone) {
      padding: 1rem;
      flex-wrap: wrap;
      align-items: center;
    }
  }

  &__first,
  &__second,
  &__third {
    flex-basis: 31%;

    @include respond(phone) {
      flex-basis: 100%;
      flex-shrink: 0;
    }
  }
  &__first {
    @media only screen and (max-width: 320px) {
      display: none;
    }
  }
  &__third {
    & > a {
      color: $color-black;

      &:hover {
        color: $color-primary;
      }
    }
  }
  &__title {
    margin-bottom: 3rem;
    font-size: 1.4rem;
    @include respond(phone) {
      margin-bottom: 1rem;
    }
  }

  &__text {
    margin-bottom: 3rem;
    @include respond(phone) {
      margin-bottom: 1rem;
    }
  }

  &__sns-box {
    display: flex;
    @media only screen and (max-width: 850px) {
      justify-content: space-between;
      flex-direction: column;
    }
  }

  &__nav {
    &-list {
      display: flex;
      flex-wrap: wrap;
    }

    &-item {
      margin: 1rem;
      flex-basis: 40%;
      @include respond(tab-port) {
        flex-basis: 30%;
      }
      @include respond(phone) {
        flex-basis: auto;
        margin: 0.5rem;
      }
    }
    &-link {
      line-height: 3.3;
      color: $color-black;
      &:hover {
        color: $color-primary;
      }
      @include respond(phone) {
        line-height: 1.7;
      }
    }
  }

  &__link {
    &:hover {
      color: $color-primary;
    }
  }

  &__bottom {
    max-width: $grid-width;
    width: 100%;
    margin: 0 auto;
    padding: 3rem 2rem;
    display: flex;
    justify-content: space-between;

    @include respond(tab-land) {
      padding: 2rem;
    }
    @include respond(phone) {
      display: flex;
      flex-direction: column;
      padding: 2rem 1rem;
    }
  }

  &__privacy {
    display: block;
    color: $color-black;
    margin-right: 0;
    @include respond(phone) {
      order: 1;
      padding-bottom: 1rem;
    }
    &:hover {
      color: $color-primary;
    }
  }

  &__copy {
    @include respond(phone) {
      order: 2;
    }
  }

  &__fixed {
    max-width: $grid-width;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: space-between;
    position: fixed;
    top: auto;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    @media only screen and (max-height: 750px) {
      position: relative;
    }
  }
}
