.fields {
  &__container {
    padding: 13rem 0;
    height: 80rem;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: linear-gradient( to bottom,
        rgba($color-white, 1) 0%,
        rgba($color-white, 0.95) 18%,
        rgba($color-white, 0.8) 35%,
        rgba($color-white, 0.1) 100%
      ),
      url('../img/fields-bg.jpg') fixed;
    background-size: cover;
    background-position: center;
    // background-attachment: fixed;
    background-repeat: no-repeat;
    @include respond(phone) {
      padding: 5rem 0;
    }
  }

  &__text {
    max-width: 92rem;
    text-align: center;
    margin: 0 auto;
  }

  &__cards {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 52rem;
    text-align: left;
    z-index: 100;
    position: relative;
    margin-bottom: 4rem;
  }

  &__card {
    width: 35rem;
    height: 46.6rem;
    border-radius: 5px;
    flex: 0;
    margin: 1.5rem 1rem;
    box-shadow: 0 0 2rem rgba($color-black, 0.2);
    transition: all 0.3s;
    &:hover {
      transform: scale(1.05);
    box-shadow: 0 0 5rem rgba($color-black, 0.2);
    }


    @include respond(phone) {
      max-width: 40rem;
    }
    &-image {
      max-width: 35rem;
      max-height: 23.3rem;
      object-fit: cover;

      @include respond(phone) {
        max-width: 40rem;
      }
    }

    &-text-box {
      margin: 4rem;
    }
    &-title {
      font-size: 2rem;
      margin-bottom: 2.5rem;
    }
    &-paragraph {
      font-size: 1.2rem;
      margin-bottom: 3rem;
      color: $color-grey-dark-2;
    }
    &-link {
      font-size: 1.4rem;
      color: $color-primary;
      text-align: center;
      margin: 0 auto;
      cursor: pointer;
      text-decoration: none;
      &:hover,
      &:active {
        color: $color-secondary;
      }
    }
  }
}
