.header {
  height: 10rem;
  line-height: 10rem;
  background-color: rgba($color-white, 0.2);

  transition: all 0.3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10000;

  @include respond(tab-land) {
    height: 6rem;
    line-height: 6rem;
  }

  &-bg {
    background-color: rgba($color-black, 0.7);
    padding: 0 2rem;
    height: 6rem;
    line-height: 6rem;
    & .header__logo {
      width: 10rem;
      height: 2.8rem;
      margin-left: 2rem;
    }
    & .header__nav {
      margin-right: 0;
      &-item {
        height: 6rem;
        line-height: 6rem;
      }
    }
  }

  &__logo {
    width: 14.7rem;
    height: 3.3rem;
    &-link {
      margin: auto 4rem;
      line-height: 0;
    }
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    margin-right: 3rem;
    @include respond(tab-land) {
      height: 6rem;
      line-height: 6rem;
    }
    &-container {
      display: flex;

      @include respond(tab-land) {
        opacity: 0;
        visibility: hidden;
      }
    }

    &-list {
      display: flex;
      @include respond(tab-land) {
        flex-direction: column;
        max-height: 100%;
      }
    }

    &-item {
      padding: 0 1.5rem;
      display: block;
      height: 10rem;
      line-height: 10rem;
      &:last-child {
        padding-right: 0;
        position: relative;
      }
      &:last-child:after {
        content: '|';
        color: $color-white;
        margin-left: 3rem;

        @include respond(tab-land) {
          content: '\2014';
          position: absolute;
          bottom: 0;
          right: 50%;
          transform: translate(50%, 50%);
        }
      }
      @include respond(tab-land) {
        height: 6rem;
        line-height: 6rem;
        text-align: center;
        width: 25rem;
        margin: 0 auto;
        transform: translateX(-100rem);
        transition: transform 0.5s cubic-bezier(0.59, 0.59, 0.63, 1.46);
        @for $i from 1 through 7 {
          &:nth-of-type(#{$i}) {
            transition-delay: 70ms * $i;
          }
        }
      }
    }

    &-link {
      text-align: center;
      color: $color-white;
      transition: all 0.3s;

      @include respond(tab-land) {
        display: block;
        margin: 0;
      }
      &:hover {
        color: $color-primary;
      }
    }
  }

  // ハンバーガー
  &__mobile-menu {
    display: none;
    outline: none;
    border: none;
    cursor: pointer;

    width: 4rem;
    height: 4rem;
    background: rgba($color-white, 0.2);
    border-radius: 50%;
    position: relative;

    @include respond(tab-land) {
      display: block;
      position: fixed;
      top: 1rem;
      right: 3rem;
      z-index: 10000;
    }
  }
  &__line {
    display: inline-block;
    width: 2.5rem;
    height: 2px;
    vertical-align: middle;
    background-color: $color-white;
    transition: all 0.3s;

    &::before,
    &::after {
      content: '';
      display: inline-block;

      position: absolute;
      width: 2.5rem;
      height: 2px;
      background-color: $color-white;
      transform: translateX(-50%);
      transition: all 0.3s;
    }
    &::before {
      top: 1rem;
      left: 50%;
    }
    &::after {
      top: 3rem;
      left: 50%;
    }
  }

  // mobile-menu クリック時.header
  &.active {
    @include respond(tab-land) {
      height: 100vh;
      width: 100vw;
      background: rgba($color-black, 0.8);
      font-size: 2.4rem;
    }
  }

  &.active &__nav {
    &-container {
      @include respond(tab-land) {
        max-height: 100%;
        width: 100%;
        opacity: 1;
        visibility: visible;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        flex-direction: column;
        align-items: center;

        & > .sns {
          margin-top: 2rem;
        }
      }
    }
    &-list {
      width: inherit;
    }
    &-item {
      transform: translateX(0);
      width: inherit;
    }
  }

  &.active &__line {
    width: 0px;
    &::before {
      transform: translate(-1.25rem, 1rem) rotate(45deg);
    }
    &::after {
      transform: translate(-1.25rem, -1rem) rotate(135deg);
    }
  }
}
