.sns {
  margin-left: 2.5rem;
  & i {
    width: 3.6rem;
    background-color: rgba($color-white, 0.3);
    height: 3.6rem;
    text-align: center;
    line-height: 3.6rem;
    border-radius: 50%;
    color: $color-white;
    padding: 0;
    margin-right: 8px;
    transition: all .3s;

    &:hover {
      background-color: $color-primary;
    }
    &.bg-grey {
      background-color: rgba($color-black, 0.3);
      &:hover {
        background-color: $color-primary;
      }
    }
  }

}