.facilities {
  &__container {
    padding-top: 13rem;
    height: 80rem;
    width: 100%;
    background: linear-gradient(
        to bottom,
        rgba($color-white, 1) 0%,
        rgba($color-white, 0.95) 18%,
        rgba($color-white, 0.8) 35%,
        rgba($color-white, 0.1) 100%
      ),
      url('../img/facilities-bg.jpg') fixed;
    background-size: cover;
    @include respond(phone) {
      padding-top: 5rem;
    }
  }

  &__text {
    max-width: 92rem;
    text-align: center;
    margin: 0 auto;
    @include respond(tab-land) {
      max-width: 90%;
    }
  }

  &__images {
    max-width: 100%;
    margin-top: -15rem;
    display: grid;
    grid-template-rows: repeat(4, 22.5rem);
    grid-template-columns: repeat(4, minmax(min-content, max-content));

    row-gap: 1rem;
    column-gap: 1rem;
    margin-bottom: 6rem;
    @include respond(phone) {
      grid-template-rows: repeat(6, max-content);
      grid-template-columns: minmax(100%, max-content);
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 100;
    object-fit: cover;
    @include respond(tab-land) {
      opacity: 0.5;
      transition: opacity 0.3s;
      &:hover {
      opacity: 1;
      }
    }
    &-container {
      background: $color-black;
      position: relative;
      overflow: hidden;

      &:nth-of-type(1) {
        grid-column: 1 / -1;
        grid-row: 1 / span 2;
      }
      &:nth-of-type(2) {
        grid-column: 1 / span 2;
        grid-row: 3 / -1;
      }

      &:nth-of-type(3) {
        grid-column: 3 / span 1;
        grid-row: 3 / span 1;
      }
      &:nth-of-type(4) {
        grid-column: 4 / span 1;
        grid-row: 3 / span 1;
      }
      &:nth-of-type(5) {
        grid-column: 3 / span 1;
        grid-row: 4 / -1;
      }
      &:nth-of-type(6) {
        grid-column: 4 / -1;
        grid-row: 4 / -1;
      }
      &::before,
      &::after {
        content: '';
        position: absolute;
        width: 50%;
        height: 100%;
        background-color: rgba($color-black, 0.5);
        bottom: 0;
        z-index: 200;
        transition: all 0.3s;
        @include respond(tab-land) {
          display: none;
        }
      }
      &::before {
        left: 0;
      }
      &::after {
        right: 0;
      }
      &:hover {
        &::before {
          transform: translateX(-100%);
        }
        &::after {
          transform: translateX(100%);
        }
      }

      @include respond(phone) {
        &:nth-of-type(3),
        &:nth-of-type(4),
        &:nth-of-type(5),
        &:nth-of-type(6) {
          grid-column: 1 / -1;
          grid-row: span 1;
        }
      }
    }
    &-title {
      position: absolute;
      z-index: 200;
      top: 3rem;
      left: 3rem;
      color: $color-white;
    }
  }

  &__popup {
    opacity: 0;
    visibility: hidden;
    width: 10%;

    position: fixed;
    top: 15%;
    left: 50%;
    transform: translateX(-50%);
    height: 80%;
    background: $color-grey-light-2;
    border-radius: 5px;
    z-index: 1100;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    transition: transform 0.3s, width 0.3s 0.3s, opacity 0.6s;

    &:target {
      opacity: 1;
      visibility: visible;
      width: 80%;
      & .facilities__popup-text-box {
        opacity: 1;
      }
    }
    @include respond(tab-port) {
      flex-direction: column;
      overflow-y: scroll;
    }

    &-times {
      position: absolute;
      top: 2rem;
      right: 2rem;
      z-index: 1200;
      background: rgba($color-white, 0.5);
      border-radius: 50%;
      width: 3rem;
      height: 3rem;
      &:hover {
        color: $color-primary;
      }
    }
    &-image {
      &-1 {
        background: url('../img/conf-hall.jpg');
        background-position: center;
      }
      &-2 {
        background: url('../img/library_small.jpg');
        background-position: left;
      }
      &-3 {
        background: url('../img/lab_small.jpg');
        background-position: center;
      }
      &-4 {
        background: url('../img/internet_small.jpg');
        background-position: center;
      }
      &-5 {
        background: url('../img/study-hall_small.jpg');
        background-position: left;
      }
      &-6 {
        background: url('../img/dorm_small.jpg');
        background-position: center;
      }
      @for $i from 1 through 6 {
        &-#{$i} {
          width: 50%;
          height: 100%;
          z-index: 1150;
          background-size: cover;
          background-repeat: no-repeat;
          @include respond(tab-port) {
            width: 100vw;
            min-height: 50rem;
          }
        }
      }
    }
    &-text-box {
      width: 50%;
      padding: 6rem;
      opacity: 0;
      transition: opacity 0.3s 0.6s;
      @include respond(tab-port) {
        width: 100%;
        padding: 2rem;
      }
    }
    &-text {
      text-align: left;
      overflow-y: auto;
      max-height: 30vh;
    }
  }
}
