.contact {
  &__container {
    padding: 0 3rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
    @include respond(tab-port) {
      flex-direction: column;
      align-items: center;
    }
    @include respond(phone) {
      width: 95%;
    }
  }

  &__text-container {
    max-width: 58%;
    @include respond(tab-port) {
      max-width: 90%;
    }
    @include respond(phone) {
      max-width: 95%;
    }
  }

  &__text {
    margin-bottom: 5rem;
  }

  &__inputs {
    max-width: 40%;
    & .input,
    & .textarea {
      margin: 0 auto 2rem;
      width: 100%;
    }
    @include respond(tab-port) {
      max-width: 80%;
      & .button {
        display: block;
        margin: 0 auto;
      }
    }
  }
}
