.courses {
  &__container {
    text-align: center;

    & button {
      margin-bottom: 7.5rem;
      @include respond(phone) {
        margin-bottom: 5rem;
      }
    }
  }

  &__text {
    margin-bottom: 5rem;
    @include respond(tab-port) {
      max-width: 90%;
      margin: 0 auto 7.5rem;
    }
    @include respond(phone) {
      margin-bottom: 5rem;
    }
  }

  &__image {
    &-box {
      max-width: 100%;
      max-height: 64.4rem;
      position: relative;
    }

    &-1 {
      max-width: 100%;
      @include respond(tab-land) {
        max-width: 95%;
      }
    }

    &-2 {
      max-width: 22.3rem;
      max-height: 64.4rem;
      position: absolute;
      bottom: 0;
      right: -2rem;

      @include respond(tab-land) {
        max-width: 20rem;
      }
      @include respond(tab-port) {
        max-width: 15rem;
      }
      @include respond(phone) {
        max-width: 10rem;
      }
    }
  }
}
