.hero {
  &__text-box {
    color: $color-white;
    padding: 0 6rem;
    @include respond(tab-port) {
      margin: 0 auto;
      text-align: center;
    }
  }

  &__accent {
    color: $color-primary;
  }

  &__paragraph {
    margin-bottom: 6rem;
    max-width: 50%;
    @include respond(tab-land) {
      max-width: 40%;
    }
    @include respond(tab-port) {
      max-width: 80%;
      margin-right: auto;
      margin-left: auto;
    }
    @include respond(phone) {
      z-index: 10;
      position: relative;
      max-width: 100%;
    }
  }

  &__buttons {
    z-index: 10;
    position: relative;
    & > .button {
      margin-right: 1.5rem;
    }
    @include respond(phone) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      & > .button {
        margin-right: 0;
        margin-bottom: 1.5rem;
      }
    }
  }

  &__image {
    height: 60rem;
    object-fit: contain;
    position: absolute;
    right: 6rem;
    bottom: 0;

    @include respond(tab-land) {
      object-fit: contain;
    }

    @include respond(tab-port) {
      display: none;
    }
  }
}
