.features {
  &__container {
    display: flex;
    @include respond(tab-land) {
      flex-wrap: wrap;
      justify-content: space-evenly;
      align-items: center;
    }
  }

  &__col {
    text-align: center;
    margin: 0 auto;
    color: $color-grey-dark;
    padding: 0 20px;

    @include respond(tab-land) {
      margin-bottom: 3rem;
    }
  }

  &__icon {
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    font-size: 4.8rem;
    line-height: 10rem;
    background-color: $color-grey-light-2;
    color: $color-primary;
    margin-bottom: 3rem;
    transition: all .3s;

    &:hover {
      background-color: $color-primary;
      color: $color-grey-light-2;
      transform: rotateY(180deg);
    }
  }

  &__main-text {
    font-size: 1.8rem;
  }

  &__sub-text {
    font-size: 1.2rem;
    line-height: 2.5;
    max-width: 30rem;
  }
}
