.section {
  &-hero {

    @include respond(phone) {
      height: 100vh;
    }
  }

  &-features {
    padding: 13rem 0;
    @include respond(phone) {
      padding: 5rem 0;
    }
  }

  &-professors {
    background: linear-gradient(
        rgba($color-primary, 0.8),
        rgba($color-primary, 0.8)
      ),
      url('../img/professors-bg.jpg');
    background-size: cover;
    height: 59.5rem;
    @include respond(phone) {
    height: auto;
    }
  }
  &-fields {
    width: 100vw;
    padding: 13rem 0;
    text-align: center;
    position: relative;
    @include respond(phone) {
      padding: 5rem 0;
    }
  }
  &-grades {
    background: linear-gradient(
        rgba($color-black, 0.5),
        rgba($color-black, 0.5)
      ),
      url('../img/grades-img-par.jpg') fixed;
    background-size: cover;
    height: 53.5rem;
    padding: 13rem 0;
    color: $color-white;
    @include respond(phone) {
      padding: 5rem 0;
      height: auto;
    }
  }
  &-courses {
    padding: 13rem 0;
    @include respond(phone) {
      padding: 5rem 0;
    }
  }
  &-subscribe {
    background: linear-gradient(
        rgba($color-secondary, 0.85),
        rgba($color-secondary, 0.85)
      ),
      url('../img/subscribe-bg.jpg');
    background-size: cover;
    color: $color-white;
  }
  &-facilities {
    position: relative;
    width: 100vw;
    padding-bottom: 13rem;
    text-align: center;
    @include respond(phone) {
      padding-bottom: 5rem;
    }
  }
  &-awards {
    width: 100vw;
    padding: 13rem 0;
    text-align: center;
    background: linear-gradient(
        rgba($color-black, 0.5),
        rgba($color-black, 0.5)
      ),
      url('../img/awards-bg.jpg');
    background-size: cover;
    @include respond(phone) {
      padding: 5rem 0;
    }
  }
  &-branches {
    padding: 13rem 0;
    @include respond(tab-land) {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin: 0 auto;
    }
    @include respond(phone) {
      padding: 5rem 0;
    }
  }
  &-contact {
    color: $color-white;
    width: 100vw;
    padding: 13rem 0;
    background: linear-gradient(
        rgba($color-tertiary, 0.85),
        rgba($color-tertiary, 0.85)
      ),
      url('../img/contact.jpg');
    background-size: cover;
    @include respond(phone) {
      padding: 5rem 0;
    }
  }
  &-subscribe-mini {
    padding: 13rem 0;
    background: $color-grey-light-3;
    margin: 0 auto;
    text-align: center;
    box-shadow: 1px 0 20px rgba($color-black, 0.3);
    @include respond(phone) {
      padding: 5rem 0;
    }
  }
}
