.grades {
  &__container {
    width: 100%;
    @include respond(tab-port) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__text-container {
    max-width: 50%;
    padding: 0 3rem;
    @include respond(tab-port) {
      max-width: 100%;
      text-align: center;
    }
  }

  &__text {
    margin-bottom: 5rem;
  }

  &__image {
    height: 100%;
    width: 50%;
    clip-path: polygon(25% 0, 100% 0, 100% 100%, 0% 100%);
    position: absolute;
    bottom: 0;
    right: 0;

    @include respond(tab-port) {
      display: none;
    }
  }
}
