body {
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 400;
  line-height: 1.7;
  color: $color-grey-dark;
  font-size: $default-font-size;
  background-color: $color-section-bg;
}

.heading-primary {
  font-size: 4.2rem;

  @include respond(tab-land) {
    font-size: 3.2rem;
  }
  @include respond(phone) {
    font-size: 2.8rem;
  }
}

.heading-secondary,
.heading-secondary-white {
  font-size: 3.6rem;
  position: relative;
  text-align: center;
  color: $color-black;
  margin-bottom: 5rem;
  @include respond(tab-land) {
    font-size: 3rem;
  }
  @include respond(phone) {
    font-size: 2.4rem;
  }
  &::after {
    content: '';
    position: absolute;
    bottom: -3rem;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 2px;
    background-color: $color-primary;
  }

  &-white {
    color: $color-white;
    &::after {
      background-color: $color-white;
    }
  }

  @include respond(tab-port) {
  }

  @include respond(phone) {
  }
}

.heading-tertiary {
  font-size: 3rem;
  margin-bottom: 6rem;
  position: relative;
  @include respond(tab-land) {
    font-size: 2.6rem;
  }
  @include respond(tab-port) {
    text-align: center;
  }
  @include respond(phone) {
    font-size: 2rem;
  }
  &::after {
    content: '';
    position: absolute;
    bottom: -3rem;
    left: 0;
    width: 100px;
    height: 2px;
    background-color: $color-white;
    @include respond(tab-port) {
      left: 50%;
      transform: translateX(-50%);
    }
  }
}

.paragraph {
  // font-size: $default-font-size;

  &:not(:last-child) {
    // margin-bottom: 3rem;
  }
}
