.branches {
  &__container {
    padding: 0 3rem;
    width: 100%;
    display: flex;
    @include respond(tab-land) {
      flex-direction: column;
      width: 90%;
      margin: 0 auto;
    }
    @include respond(phone) {
      width: 95%;
    }
  }

  &__text-box {
    max-width: 40%;
    @include respond(tab-land) {
      max-width: 100%;
      margin: 0 auto 3rem;
    }

    & p:nth-of-type(1) {
      margin-bottom: 4.5rem;
    }
    & p:nth-of-type(2) {
      margin-bottom: 2rem;
    }

    & > a {
      text-decoration: none;
      display: inline-block;
      margin-bottom: 2rem;
      color: $color-secondary;
      &:hover {
        color: $color-primary;
      }
    }
  }

  &__image {
    width: 63.5rem;
    height: 60rem;
    margin-left: 3rem;
    @include respond(tab-land) {
      margin: 0 auto;
      max-width: 100%;
    }
  }
}
